













































































































































































































































































































































@import "./src/scss/style";

.page {
    background:  url("/klever/img/blob.png") no-repeat right 0 bottom 0;
    height: 100vh;
    overflow: hidden;
    @include _768 {
        height: 100%;
    }

    &-item {
        display: flex;
        flex-direction: column;
        width: 100vw;
        height: 100%;
        overflow-y: inherit;
        overflow-x: hidden;

        position: relative;

        & > * {
            position: relative;
            z-index: 5;
        }

    }

    &-main {
        height: 100%;
        background:  url("/klever/img/slide-1.png") no-repeat right 0 bottom 0;
    }

    &-about {
        height: 100%;
        background:  url("/klever/img/slide-2.png") no-repeat right 0 bottom 0;
    }

    &-portfolio {
        height: 100%;
        background:  url("/klever/img/slide-3.png") no-repeat right 0 bottom 0;
    }

    &-contacts {
        height: 100%;
        background:  url("/klever/img/slide-4.png") no-repeat right 0 bottom 0;
    }
}
